import React, {
  createContext,
  useContext,
  useState,
  useCallback,
  useMemo,
} from "react";

const GameContext = createContext();

export const useGame = () => useContext(GameContext);

export const GameProvider = ({ children }) => {
  const [gameStarted, setGameStarted] = useState(false);
  const [selectedEnglish, setSelectedEnglish] = useState(null);
  const [selectedFrench, setSelectedFrench] = useState(null);
  const [matches, setMatches] = useState([]);
  const [isGraded, setIsGraded] = useState(false);
  const [score, setScore] = useState(0);
  const [englishWords, setEnglishWords] = useState([]);
  const [frenchWords, setFrenchWords] = useState([]);
  const [wordPairs, setWordPairs] = useState([]);

  const pairs = useMemo(
    () => [
      { english: "cat", french: "chat" },
      { english: "dog", french: "chien" },
      { english: "bird", french: "oiseau" },
      { english: "fish", french: "poisson" },
      { english: "house", french: "maison" },
      { english: "book", french: "livre" },
      { english: "tree", french: "arbre" },
      { english: "water", french: "eau" },
      { english: "sun", french: "soleil" },
      { english: "moon", french: "lune" },
    ],
    []
  );

  const startGame = useCallback(() => {
    setWordPairs(pairs);
    const shuffledPairs = [...pairs].sort(() => Math.random() - 0.5);
    const shuffledEnglish = shuffledPairs.map((pair) => pair.english);
    const shuffledFrench = shuffledPairs.map((pair) => pair.french);

    setEnglishWords(shuffledEnglish);
    setFrenchWords(shuffledFrench);
    setMatches([]);
    setSelectedEnglish(null);
    setSelectedFrench(null);
    setIsGraded(false);
    setScore(0);
    setGameStarted(true);
  }, [pairs]);

  const gradeGame = useCallback(() => {
    let correctCount = 0;
    matches.forEach((match) => {
      const englishWord = englishWords[match.englishIndex];
      const frenchWord = frenchWords[match.frenchIndex];
      const isCorrect = wordPairs.some(
        (pair) => pair.english === englishWord && pair.french === frenchWord
      );
      if (isCorrect) correctCount++;
    });
    setScore(correctCount);
    setIsGraded(true);
  }, [matches, englishWords, frenchWords, wordPairs]);

  const resetGame = useCallback(() => {
    setGameStarted(false);
    setMatches([]);
    setSelectedEnglish(null);
    setSelectedFrench(null);
    setIsGraded(false);
    setScore(0);
  }, []);

  const value = {
    gameStarted,
    selectedEnglish,
    selectedFrench,
    matches,
    isGraded,
    score,
    englishWords,
    frenchWords,
    wordPairs,
    setSelectedEnglish,
    setSelectedFrench,
    setMatches,
    startGame,
    gradeGame,
    resetGame,
  };

  return <GameContext.Provider value={value}>{children}</GameContext.Provider>;
};
