import React, { memo, useCallback } from "react";
import { useGame } from "../context/GameContext";
import WordItem from "./WordItem";

const WordColumn = memo(({ isEnglish, onClick }) => {
  const {
    englishWords,
    frenchWords,
    selectedEnglish,
    selectedFrench,
    matches,
    isGraded,
    wordPairs,
  } = useGame();

  const words = isEnglish ? englishWords : frenchWords;
  const selectedWord = isEnglish ? selectedEnglish : selectedFrench;

  const getMatchStyle = useCallback(
    (match) => {
      if (!isGraded) {
        return {
          strokeColor: "silver",
          strokeWidth: 3,
        };
      }

      const englishWord = englishWords[match.englishIndex];
      const frenchWord = frenchWords[match.frenchIndex];
      const isCorrect = wordPairs.some(
        (pair) => pair.english === englishWord && pair.french === frenchWord
      );

      return {
        strokeColor: isCorrect ? "green" : "red",
        strokeWidth: 3,
      };
    },
    [isGraded, wordPairs, englishWords, frenchWords]
  );

  const isWordMatched = useCallback(
    (word, isEnglish) => {
      return matches.some((match) =>
        isEnglish
          ? match.englishIndex === word.index
          : match.frenchIndex === word.index
      );
    },
    [matches]
  );

  const getWordRelations = useCallback(
    (index) => {
      if (!isEnglish) return [];

      return matches
        .filter((match) => match.englishIndex === index)
        .map((match) => ({
          targetId: `french-${match.frenchIndex}`,
          targetAnchor: "left",
          sourceAnchor: "right",
          style: getMatchStyle(match),
        }));
    },
    [matches, isEnglish, getMatchStyle]
  );

  return (
    <div
      className={`column ${
        selectedEnglish || selectedFrench ? "selected" : ""
      }`}
    >
      <h2>{isEnglish ? "English" : "French"}</h2>
      <div className="words-list">
        {words.map((word, index) => (
          <WordItem
            key={`${isEnglish ? "english" : "french"}-${index}`}
            word={word}
            index={index}
            isEnglish={isEnglish}
            isSelected={selectedWord?.index === index}
            isMatched={isWordMatched({ word, index }, isEnglish)}
            onClick={onClick}
            relations={getWordRelations(index)}
          />
        ))}
      </div>
    </div>
  );
});

export default WordColumn;
