import React, { memo } from "react";
import { ArcherElement } from "react-archer";

const WordItem = memo(
  ({ word, index, isEnglish, isSelected, isMatched, onClick, relations }) => {
    return (
      <ArcherElement
        id={`${isEnglish ? "english" : "french"}-${index}`}
        relations={relations}
      >
        <div
          className={`word-item ${isSelected ? "selected" : ""} ${
            isMatched ? "matched" : ""
          }`}
          onClick={() => onClick(word, index)}
        >
          {word}
        </div>
      </ArcherElement>
    );
  }
);

export default WordItem;
