import React, { memo, useCallback } from "react";
import { ArcherContainer } from "react-archer";
import { GameProvider, useGame } from "./context/GameContext";
import WordColumn from "./components/WordColumn";
import GameControls from "./components/GameControls";
import "./WordMatchingGame.css";

const GameContent = memo(() => {
  const {
    gameStarted,
    selectedEnglish,
    selectedFrench,
    setMatches,
    setSelectedEnglish,
    setSelectedFrench,
    startGame,
  } = useGame();

  const handleWordClick = useCallback(
    (word, index, isEnglish) => {
      const selectedWord = isEnglish ? selectedFrench : selectedEnglish;
      const setSelectedWord = isEnglish
        ? setSelectedEnglish
        : setSelectedFrench;
      const setOtherSelectedWord = isEnglish
        ? setSelectedFrench
        : setSelectedEnglish;

      if (selectedWord) {
        setMatches((prevMatches) =>
          prevMatches.filter(
            (match) =>
              (isEnglish ? match.englishIndex : match.frenchIndex) !== index &&
              (isEnglish ? match.frenchIndex : match.englishIndex) !==
                selectedWord.index
          )
        );

        const newMatch = {
          englishIndex: isEnglish ? index : selectedWord.index,
          frenchIndex: isEnglish ? selectedWord.index : index,
        };

        setMatches((prevMatches) => [...prevMatches, newMatch]);
        setOtherSelectedWord(null);
      } else {
        setSelectedWord({ word, index });
      }
    },
    [
      selectedEnglish,
      selectedFrench,
      setMatches,
      setSelectedEnglish,
      setSelectedFrench,
    ]
  );

  if (!gameStarted) {
    return (
      <div className="game-container">
        <h1>Word Matching Game</h1>
        <button className="start-button" onClick={startGame}>
          GO!
        </button>
      </div>
    );
  }

  return (
    <div className="game-container">
      <h1>Word Matching Game</h1>
      <GameControls />
      <ArcherContainer strokeColor="red" strokeWidth={3}>
        <div className="columns-container">
          <WordColumn
            isEnglish={true}
            onClick={(word, index) => handleWordClick(word, index, true)}
          />
          <WordColumn
            isEnglish={false}
            onClick={(word, index) => handleWordClick(word, index, false)}
          />
        </div>
      </ArcherContainer>
    </div>
  );
});

const WordMatchingGame = () => {
  return (
    <GameProvider>
      <GameContent />
    </GameProvider>
  );
};

export default WordMatchingGame;
